<template>
    <div id="status">
        <loadings ref="load"></loadings>
        <navbar :isActive="page_belong"></navbar>
        <div class="container mundb-standard-container" style="min-height: 70vh">
            <paper-card style="min-height: 350px">
                <div>
                    <div class="no-scrollbar table-responsive">
                        <table id="result-tab" class="table">
                            <thead>
                            <tr>
                                <th scope="col">
                                    RunID
                                </th>
                                <th scope="col">
                                    <!--<form class="form-inline"-->
                                    <!--style="margin-bottom: 0px" @submit.prevent="">-->
                                    <input v-model="search.user_info" class="form-control search-query"
                                           type="text" style="text-align: center"
                                           name="ufo" placeholder="User" @keydown.enter="choose_user_info">
                                    <!--</form>-->
                                </th>
                                <th scope="col">
                                    <input v-model="search.problem_id" class="form-control search-query"
                                           type="text" style="text-align: center"
                                           name="pid" placeholder="ID" @keydown.enter="choose_problem_id">
                                </th>
                                <th scope="col">
                                    <input v-model="search.result"
                                           v-bind:class="['form-control', 'search-query', trans_result_color(page_setting.result)]"
                                           type="text" style="text-align: center;background-color: white;"
                                           name="res" placeholder="Result" readonly="readonly"
                                           id="dropdownMenuResult" @focus="switch_result_pannel(1, true)"
                                           @blur="switch_result_pannel(1, false)"/>
                                    <div class="pannel result-pannel" @mouseover="switch_result_pannel(0, true)"
                                         @mouseleave="switch_result_pannel(0, false)"
                                         v-bind:style="{width: '148.8px', display: result_pannel.a || result_pannel.b? '': 'none'}">
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-grey-text"
                                             @click="switch_result(null)">All
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-green-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_ACCEPTED)">Accepted
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-blue-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_COMPILING)">Compiling
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-orange-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_COMPILATION_ERROR)">Compile Error
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-red-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_WRONG_ANSWER)">Wrong Answer
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-red-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_PRESENTATION_ERROR)">Presentation
                                            Error
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-red-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_RUNTIME_ERROR)">Runtime Error
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-deep-purple-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_TIME_LIMIT_EXCEEDED)">Time Limit
                                            Exceed
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-deep-purple-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_MEMORY_LIMIT_EXCEEDED)">Memory Limit
                                            Exceed
                                        </div>
                                        <div class="dropdown-divider"></div>
                                        <div class="pannel-item wemd-deep-purple-text"
                                             @click="switch_result(GLOBAL.OJ_RESULT_OUTPUT_LIMIT_EXCEEDED)">Output Limit
                                            Exceed
                                        </div>
                                        <div class="dropdown-divider"></div>
                                    </div>
                                </th>
                                <th scope="col">
                                    <div>
                                        <input style="width: 100%;background-color:white;text-align: center" v-model="search.lang"
                                               placeholder="Lang" class="form-control " type="text"
                                               name="language" readonly="readonly"
                                               @focus="switch_lang_pannel(1, true)"
                                               @blur="switch_lang_pannel(1, false)">
                                        <div class="pannel lang-pannel" @mouseover="switch_lang_pannel(0, true)"
                                             @mouseleave="switch_lang_pannel(0, false)"
                                             v-bind:style="{width: '148.8px', display: lang_pannel.a || lang_pannel.b? '': 'none'}">
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(null)">All
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_C)">C
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_CPLUS)">C++
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_PASCAL)">Pascal
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_JAVA)">Java
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_RUBY)">
                                                Ruby
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_BASH)">Bash
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_PYTHON)">Python
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_PHP)">PHP
                                            </div>
                                            <div class="dropdown-divider"></div>
                                            <div class="pannel-item"
                                                 @click="switch_lang(GLOBAL.OJ_LANG_PERL)">Perl
                                            </div>
                                            <div class="dropdown-divider"></div>
                                        </div>
                                    </div>
                                </th>
                                <th scope="col">
                                    Memory
                                </th>
                                <th scope="col">
                                    Time
                                </th>
                                <th scope="col">
                                    Code Length
                                </th>
                                <th scope="col">
                                    Submit Time
                                </th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr v-for="saved_solution in solution" v-bind:key="key">
                                <td>{{saved_solution.solutionId}}</td>
                                <td>{{saved_solution.nick}}</td>
                                <td><a style="text-decoration: none"
                                       v-bind:href="'/problem.html?pid=' + saved_solution.problemId">CAOJ{{saved_solution.problemId}}</a>
                                </td>
                                <td v-bind:class="[trans_result_color(saved_solution.result)]">
                                    {{trans_result_string(saved_solution.result)}}
                                </td>
                                <td>{{trans_lang_string(saved_solution.language)}}</td>
                                <td>{{saved_solution.memory == -1? "---": saved_solution.memory/1024+' KB'}}</td>
                                <td>{{saved_solution.time == -1? "---": saved_solution.time+' ms'}}</td>
                                <td>{{saved_solution.codeLength}}</td>
                                <td>{{diff_time(saved_solution.createTime)}}</td>
                            </tr>
                            </tbody>
                        </table>
                        <empty-container v-if="solution != null && solution.length <= 0">
                            <svg t="1605936727818" class="icon" viewBox="0 0 1024 1024" version="1.1"
                                 xmlns="http://www.w3.org/2000/svg" p-id="3621" width="100" height="100">
                                <path d="M576 928c-6 0-11.9-1.7-17.1-5-9.3-5.9-14.9-16.1-14.9-27V554.3c0-12.4 7.1-23.6 18.3-28.9l288-136.1c9.9-4.7 21.5-4 30.8 1.9s14.9 16.1 14.9 27v341.7c0 12.4-7.1 23.6-18.3 28.9l-288 136.1c-4.4 2.1-9 3.1-13.7 3.1z m32-353.5v271l224-105.8v-271L608 574.5zM448 928c-4.7 0-9.3-1-13.7-3.1l-288-136.1c-11.2-5.3-18.3-16.6-18.3-28.9V418.2c0-11 5.6-21.2 14.9-27 9.3-5.9 20.9-6.6 30.8-1.9l288 136.1c11.2 5.3 18.3 16.6 18.3 28.9V896c0 11-5.6 21.2-14.9 27-5.2 3.3-11.1 5-17.1 5zM192 739.7l224 105.8v-271L192 468.7v271zM512 501.1c-4.7 0-9.4-1-13.8-3.1l-352-168.1c-11.2-5.4-18.3-16.7-18.2-29.2 0.1-12.4 7.4-23.7 18.8-28.8l352-159.9c8.4-3.8 18.1-3.8 26.5 0l352 159.9c11.3 5.1 18.6 16.4 18.8 28.8 0.1 12.4-7 23.8-18.2 29.2L525.8 498c-4.4 2-9.1 3.1-13.8 3.1zM235.8 301.7L512 433.6l276.2-131.9L512 176.2 235.8 301.7z"
                                      p-id="3622" fill="#7a8e97"></path>
                            </svg>
                            <p>Nothing matches.</p>
                        </empty-container>
                    </div>
                </div>
            </paper-card>
        </div>
        <footers></footers>
    </div>

</template>

<script>
    /* eslint no-useless-escape: "off"*/
    /* eslint vue/valid-v-for: "off"*/
    import navBar from "@/components/navbar/navbar";
    import Footers from "@/components/footer/footer";
    import Loadings from "../../components/loading/loading";
    import fun from '@/assets/js/OJFunction'
    // import {hex_md5} from "@/assets/js/include"
    // import VueResource from 'vue-resource'
    // import Vue from 'vue'
    export default {
        name: "status",
        components: {
            'navbar': navBar,
            Loadings,
            Footers
        },
        data() {
            return {
                page_belong: 'status',
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: "",
                    page: null,
                    result: null,
                    user_info: null,
                    problem_id: null,
                    lang: null
                },
                search: {
                    user_info: null,
                    problem_id: null,
                    result: null,
                    lang: null
                },
                result_pannel: {
                    a: false,
                    b: false
                },
                lang_pannel: {
                    a: false,
                    b: false
                },
                solution: null,
            }
        },
        methods: {
            diff_time: function (time) {
                time = fun.diff_time(time);
                console.log(time);
                if (time.year >= 1) {
                    if (time.year == 1) return time.year + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_YEAR.ONE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                    else return time.year + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_YEAR.MORE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                } else if (time.month >= 1) {
                    if (time.month == 1) return time.month + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_MONTH.ONE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                    else return time.month + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_MONTH.MORE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                } else if (time.week >= 1) {
                    if (time.week == 1) return time.week + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_WEEK.ONE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                    else return time.week + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_WEEK.MORE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                } else if (time.day >= 1) {
                    if (time.day == 1) return time.day + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_DAY.ONE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                    else return time.day + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_DAY.MORE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                } else if (time.hour >= 1) {
                    if (time.hour == 1) return time.hour + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_HOUR.ONE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                    else return time.hour + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_HOUR.MORE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                } else if (time.minute >= 1) {
                    if (time.minute == 1) return time.minute + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_MINUTE.ONE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                    else return time.minute + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_MINUTE.MORE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                } else {
                    if (time.second <= 1) return time.second + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_SECOND.ONE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                    else return time.second + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_SECOND.MORE') + this.$t('WARN.OJ_MESSAGE_TIME_AGO.OJ_AGO');
                }
            },
            getUrlKey: function (key) {
                return fun.getUrlKey(key);
            },
            addUrlParm: function (key, value) {
                if (fun.getUrlKey("result") != null) this.page_setting.result = fun.getUrlKey("result");
                if (fun.getUrlKey("uinfo") != null) this.page_setting.user_info = fun.getUrlKey("uinfo");
                if (fun.getUrlKey("pid") != null) this.page_setting.problem_id = fun.getUrlKey("pid");
                if (fun.getUrlKey("lang") != null) this.page_setting.lang = fun.getUrlKey("lang");

                if ("result" == key) this.page_setting.result = value != null ? value : null;
                if ("uinfo" == key) this.page_setting.user_info = value != null ? value : null;
                if ("pid" == key) this.page_setting.problem_id = value != null ? value : null;
                if ("lang" == key) this.page_setting.lang = value != null ? value : null;
            },
            getUrlString: function () {
                var str = "";
                if (this.page_setting.result != null) str += "&result=" + encodeURIComponent(this.page_setting.result);
                if (this.page_setting.user_info != null) str += "&uinfo=" + encodeURIComponent(this.page_setting.user_info);
                if (this.page_setting.problem_id != null) str += "&pid=" + encodeURIComponent(this.page_setting.problem_id);
                if (this.page_setting.lang != null) str += "&lang=" + encodeURIComponent(this.page_setting.lang);
                return str.replace("&", "?");
            },
            choose_user_info: function () {
                this.search.user_info = this.search.user_info == "" ? null : this.search.user_info;
                this.addUrlParm("uinfo", this.search.user_info);
                var url =  "/status.html" + this.getUrlString();
                window.location.href = url;
            },
            choose_problem_id: function () {
                this.search.problem_id = this.search.problem_id == "" ? null : this.search.problem_id;
                this.addUrlParm("pid", this.search.problem_id);
                var url =  "/status.html" + this.getUrlString();
                window.location.href = url;
            },
            switch_result_pannel: function (n, t) {
                if (n == 1) this.result_pannel.a = t;
                else this.result_pannel.b = t;
            },
            switch_result: function (r) {
                this.addUrlParm("result", r);
                var url = "/status.html" + this.getUrlString();
                window.location.href = url;
            },
            switch_lang_pannel: function (n, t) {
                if (n == 1) this.lang_pannel.a = t;
                else this.lang_pannel.b = t;
            },
            switch_lang: function (r) {
                this.addUrlParm("lang", r);
                var url =  "/status.html" + this.getUrlString();
                window.location.href = url;
            },
            trans_result_string: function (r) {
                if (r == null) return "";
                else {
                    var result = this.GLOBAL.OJ_MESSAGE_RESULT_STRING.get(r);
                    return this.$t("ANS." + result);
                }
            },
            trans_result_color: function (r) {
                if (r == null) return "";
                else {
                    var result = this.GLOBAL.OJ_MESSAGE_RESULT_COLOR_CLASS.get(r);
                    return result;
                }
            },
            trans_lang_string: function (r) {
                if (r == null) return "";
                else {
                    var result = this.GLOBAL.OJ_MESSAGE_LANG_STRING.get(r);
                    return this.$t("LANG." + result);
                }
            },
            test: function () {
                alert("555");
            }
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            console.log(this.page_setting.access_token);
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.page_setting.result = fun.getUrlKey("result") == null ? null : Number(fun.getUrlKey("result"));
            this.search.result = this.trans_result_string(this.page_setting.result);
            this.page_setting.user_info = this.search.user_info = fun.getUrlKey("uinfo");
            this.page_setting.problem_id = this.search.problem_id = fun.getUrlKey("pid") == null ? null : Number(fun.getUrlKey("pid"));
            this.page_setting.lang = fun.getUrlKey("lang") == null ? null : Number(fun.getUrlKey("lang"));
            this.search.lang = this.trans_lang_string(this.page_setting.lang);
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/status',
                {
                    language: this.page_setting.language,
                    result: this.page_setting.result,
                    user_info: this.page_setting.user_info,
                    problem_id: this.page_setting.problem_id,
                    lang: this.page_setting.lang
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                var error_type = res.body.OJ_ERROR_TYPE;
                this.solution = res.body.OJ_SOLUTION;
                console.log(this.solution);
                // console.log(error_type == this.GLOBAL.OJ_ERROR_NO_ERROR);
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {

                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];
                    if (res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_ADMIN) || res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_SUPER_ADMIN) ||
                        res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_PROBLEM_ADMIN)) {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_ADMIN);
                    } else {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_USER);
                    }
                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                }

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;
            });
        }
    }
</script>

<style lang="scss" scoped>
    #status {

        empty-container {
            display: block;
            text-align: center;
            margin-bottom: 4rem;
            margin-top: 4rem;

            p {
                margin-left: 20px;
                display: inline-block;
                font-size: 20px;
                font-weight: bold;
                font-family: "Roboto-slab";
            }
        }

        .table thead th, .table td, .table tr {
            vertical-align: middle;
            text-align: center;
            font-size: 0.75rem;
            color: rgba(0, 0, 0, 0.93);
            transition: .2s ease-out .0s;
        }

        paper-card {
            display: block;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 30px;
            border-radius: 4px;
            transition: .2s ease-out .0s;
            color: #7a8e97;
            background: #fff;
            padding: 1rem;
            position: relative;
            border: 1px solid rgba(0, 0, 0, 0.15);
            margin-bottom: 2rem;
        }

        tr input.form-control {
            font-weight: 500;
            font-size: 0.75rem;
            color: rgba(0, 0, 0, 0.93);
            transition: .2s ease-out .0s;
        }

        tr input.form-control::-webkit-input-placeholder {
            font-weight: 500;
            font-size: 0.75rem;
            color: rgba(0, 0, 0, 0.42);
            transition: .2s ease-out .0s;
        }

        tr input.form-control::-moz-placeholder {
            font-weight: 500;
            font-size: 0.75rem;
            color: rgba(0, 0, 0, 0.42);
            transition: .2s ease-out .0s;
        }

        tr input.form-control:-ms-input-placeholder {
            font-weight: 500;
            font-size: 0.75rem;
            color: rgba(0, 0, 0, 0.42);
            transition: .2s ease-out .0s;
        }

        tr input.form-control:-moz-placeholder {
            font-weight: 500;
            font-size: 0.75rem;
            color: rgba(0, 0, 0, 0.42);
            transition: .2s ease-out .0s;
        }

        .pannel {
            height: 200px;
            overflow: hidden scroll;
            position: absolute;
            cursor: pointer;
            background-color: white;
            border: 0.5px solid lightgray;
            border-radius: 2px;
        }

        .result-pannel .pannel-item, .lang-pannel .pannel-item {
            padding: 10px 15px 10px 15px;
        }

        .result-pannel::-webkit-scrollbar, #source-menu::-webkit-scrollbar,
        .lang-pannel::-webkit-scrollbar {
            width: 0px;
            height: 16px;
            background-color: #F5F5F5;
            border-radius: 4px;
        }
        .result-pannel::-webkit-scrollbar-track, #source-menu::-webkit-scrollbar-track,
        .lang-pannel::-webkit-scrollbar-track {
            /*-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);*/
            border-radius: 0px;
            background-color: white;
        }
        .result-pannel::-webkit-scrollbar-thumb, #source-menu::-webkit-scrollbar-thumb,
        .lang-pannel::-webkit-scrollbar-thumb {
            border-radius: 0px;
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: #555;
        }

        .pannel-item {
            transition: background-color 0.6s;
            font-size: 10px;
        }
    }

</style>